import React from "react";
import { Link } from "react-router-dom";

const Header = () => {
  return (
    <header style={headerStyle}>
      <h1 style={ueberschrift}>
        to<span style={spanStyle}>do</span>
      </h1>
      <Link to="/" style={linkStyle}>
        Home{" "}
      </Link>
      <span style={trennStyle}>|</span>
      <Link to="/About" style={linkStyle}>
        About{" "}
      </Link>
    </header>
  );
};

const headerStyle = {
  color: "white",
  textAlign: "center",
  padding: "10px"
};
const trennStyle = {
  color: "#7f8c8d",
  marginRight: "5px"
};
const linkStyle = {
  color: "#7f8c8d",
  textDecoration: "none"
};

const ueberschrift = {
  fontSize: "64px",
  fontWeight: "300",
  margin: "80px 0 25px",
  color: "#7f8c8d"
};

const spanStyle = {
  fontWeight: "700"
};
export default Header;
